.feedback__header {
    font-family: 'Inter';
    font-weight: 500;
    font-size: 36px;
    line-height: 44px;
    text-align: center;
}

.feedback__input {
    display: block;
    padding: 20px;
    width: 100%;
    min-height: 300px;
    margin-top: 30px;
    resize: none;
    outline: none;
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid #FF8540;
    border-radius: 10px;
    font-family: 'Inter';
    font-size: 24px;
    line-height: 30px;
}

.feedback__btns {
    display: flex;
    justify-content: flex-end;
    gap: 68px;
    margin-top: 40px;
}

.feedback__btn {
    padding: 12px 20px;
    border: 1px solid #000000;
    border-radius: 10px;
    color: #212426;
    font-family: 'Inter';
    font-weight: 500;
    font-size: 28px;
    line-height: 39px;
}

.feedback__btn_green {
    background-color: #B2F470;
}

@media (width <= 800px) {
    .feedback__header {
        font-size: 1.4rem;
        line-height: 1.4rem;
    }

    .feedback__input {
        margin: 0;
        font-size: 1rem;
        padding: 10px 14px;
    }

    .feedback__btn {
        padding: 18px 10px;
        font-size: 1rem;
        line-height: 1rem;
        flex-grow: 1;
    }

    .feedback__btns {
        margin-top: 40px;
        gap: 20px;
    }
}
